<template>
  <div class="seller" id="seller">
    <Scroll :height="sellerHeight">
      <div class="seller-content">
        <div class="overview">
          <h1 class="title">{{seller.name}}</h1>
          <div class="desc">
            <star class="star" :size="36" :score="seller.score"></star>
            <span class="text">({{seller.ratingCount}})</span>
            <span class="text">月售{{seller.sellCount}}单</span>
          </div>
          <ul class="remark">
            <li class="block">
              <h2>起送价</h2>
              <div class="content">
                <span class="stress">{{seller.minPrice}}元</span>
              </div>
            </li>
            <li class="block">
              <h2>商家配送</h2>
              <div class="content">
                <span class="stress">{{seller.deliveryPrice}}元</span>
              </div>
            </li>
            <li class="block">
              <h2>平均配送时间</h2> 
              <div class="content">
                <span class="stress">{{seller.deliveryTime}}分钟</span>
              </div>
            </li>
          </ul>
          <div class="favorite" @click="toggleFavorite">
            <span class="icon-favorite" :class="{'active':favorite}"></span>
            <span class="text">{{favoriteText}}</span>
          </div>
        </div>
        <split></split>
        <div class="bulletin">
          <h1 class="title">公告与活动</h1>
          <div class="content-wrapper border-1px">
            <p class="content">{{seller.bulletin}}</p>
          </div>
          <ul v-if="seller.supports" class="supports">
            <li class="support-item border-1px" v-for="item in seller.supports">
              <span class="icon" :class="classMap[item.type]"></span>
              <span class="text">{{item.description}}</span>
            </li>
          </ul>
        </div>
        <split></split>
        <div class="pics">
          <h1 class="title">商家实景</h1>
          <div class="pic-wrapper">
            <Scroll height="110">
              <ul class="pic-list">
                <li class="pic-item" v-for="pic in seller.pics">
                  <img :src="pic" alt="实景图片" width="120" height="90">
                </li>
              </ul>
            </Scroll>
          </div>
        </div>
        <split></split>
        <div class="info">
          <h1 class="title border-1px">商家信息</h1>
          <ul>
            <li class="info-item" v-for="info in seller.infos">{{info}}</li>
          </ul>
        </div>
      </div>
    </Scroll>
  </div>
</template>

<script type="text/ecmascript-6">
import star from '../star/star'
import split from '../split/split'
import {saveToLocal, loadFromLocal} from '../../common/js/store';
export default {
  props: {
    seller: {
      type: Object
    }
  },
  data(){
    return {
      sellerHeight: 0,
      favorite: (() => {
        return loadFromLocal(this.seller.id,'favorite',false);
      })()
    }
  },
  computed: {
    favoriteText(){
      return this.favorite ? '已收藏' : '收藏'
    }
  },
  created() {
    this.classMap = ['decrease','discount','guarantee','invoice','special'];
    this.$nextTick(() => {
      this.sellerHeight = document.getElementById("seller").clientHeight;
    })
  },
  methods: {
    toggleFavorite(){
      this.favorite = !this.favorite;
      saveToLocal(this.seller.id, 'favorite', this.favorite)
    }
  },
  components: {
    star,
    split
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../../common/stylus/mixin.styl"
  .seller
    position absolute
    top 174px
    bottom 0
    left 0
    width 100%
    overflow hidden
    .overview
      position relative
      padding 18px
      .title
        margin-bottom 8px
        line-height 14px
        font-size 14px
        color rgb(7,17,27)
      .desc
        padding-bottom 18px
        border-1px(rgba(7,17,27,0.1))
        font-size 0
        .star
          display inline-block
          margin-right 8px
          vertical-align top
        .text
          display inline-block
          margin-right 12px
          line-height 18px
          vertical-align top
          font-size 10px
          color rgb(77,85,93)
      .remark
        display flex
        padding-top 18px
        .block
          flex 1
          text-align center
          border-right 1px solid  rgba(7,17,27,0.1)
          &:last-child
            border none 
          h2
            margin-bottom 4px
            line-height 10px
            font-size 10px
            color rgb(147,153,159)
          .content
            line-height 24px
            font-size 10px
            color rgb(7,17,27)
            .stress
              font-size 24px
      .favorite
        position absolute
        right 18px
        top 18px
        width 58px
        text-align center
        .icon-favorite
          display block
          margin-bottom 4px
          line-height 24px
          font-size 24px
          color #d4d6d9
          &.active
            color rgb(240,20,20)
        .text
          line-height 10px
          font-size 10px
          color rgb(47,85,93)
    .bulletin
      padding 18px 18px 0
      .title
        margin-bottom 8px
        line-height 14px
        font-size 14px
        color rgb(7,17,27)
      .content-wrapper
        padding 0 12px 16px
        border-1px(rgba(7,17,27,0.1))
        .content
          line-height 24px
          font-size 12px
          color rgb(240,20,20)
      .supports
        .support-item
          padding 16px 12px
          border-1px(rgba(7,17,27,0.1))
          font-size 0
          &:last-child
            border-none()
          .icon
            display inline-block
            vertical-align top
            width 16px
            height 16px
            margin-right 6px
            background-size 16px 16px
            background-repeat no-repeat
            &.decrease
              bg-image('../../../resource/img/decrease_4')
            &.discount
              bg-image('../../../resource/img/discount_4')
            &.guarantee
              bg-image('../../../resource/img/guarantee_4')
            &.invoice
              bg-image('../../../resource/img/invoice_4')
            &.special
              bg-image('../../../resource/img/special_4')
          .text
            line-height 16px
            font-size 12px
            color rgb(7,17,27)
    .pics
      padding 18px
      .title
        margin-bottom 12px
        line-height 14px
        font-size 14px
        color rgb(7,17,27)
      .pic-wrapper
        width 100%
        overflow hidden
        white-space nowrap// 不折行
        .pic-list
          font-size 0
          .pic-item
            display inline-block
            margin-right 6px
            width 120px
            height 90px
            &:last-child
              margin 0
    .info
      padding 18px 18px 0 18px
      color rgb(7,17,27)
      .title
        padding-bottom 12px
        line-height 14px
        font-size 14px
        border-1px(rgba(7,17,27,0.1))
      .info-item
        padding 16px 12px
        line-height 16px
        border-1px(rgba(7,17,27,0.1))
        font-size 12px
        &:last-child
          border-none()
</style>



